
import Toast from 'vux/src/components/toast/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import getDataService from '../../../service/getData.js'
import upDataSevice from '../../../service/upData.js'
export default{
components:{
Toast,Group,XHeader,Checker,XInput,PopupPicker,XTextarea
},
created(){


getDataService.dyeType().then(res=>{
var arr=[];
for(var i in res){
arr.push({value:''+res[i].code , name:res[i].name});
}
this.dyeTypeData = arr

},res=>console.log(res))
},
data(){
return {
data:null,
dyeTypeData:[], // 
user_yeType:{
// "applicantId":1, // ID
"specialDye":[], // 
"ihcMarker":'', // 
"note":"", // 
},
toastText:'',
toastShow:false,
}
},
mounted (){

console.log(2222)
console.log('deviceid: ', this.data=this.$route.query.routeParams);
},
computed:{
ihcMarker_arr(){ // 
}
},
methods:{

onHide(){
// this.$router.push({name:"addApplicant"});
},
save(){
console.log(this.user_yeType)

if( this.user_yeType.ihcMarker == ''){

this.toastText='';
this.toastShow=true;
}else{

var applicantId = this.$store.state.applicant.default.id
var specialDye_arr = this.$store.state.specialDye[0]

// 
var ihcMarker_arr=[];
var str = this.user_yeType.ihcMarker; // 
str = str.replace(/\s+/g, ' ') // 
.replace(/[;]+/g, ';')// ;;
.replace(/; /g, ';') // 
.replace(/ ;/g, ';') // 
.replace(/;+/g, ';') // 
var ihcMarker_arr = str.split(';')


// 
var ihcBlocks_arr = [];
for(var i=0; i<= specialDye_arr.length-1; i++){
var data = specialDye_arr[i];
ihcBlocks_arr.push({
serialNumber : (data.pathologySerialNumber), // #
subId: (data.subId)+'', // #
specialDye: parseInt((this.user_yeType.specialDye)[0]), // #
ihcMarker: ihcMarker_arr, // #
note: (this.user_yeType.note) // #

})
}

// 
var toService={
"applicantId": applicantId,
"ihcBlocks":ihcBlocks_arr
}

console.log(toService)
// 
upDataSevice.up_specialDye(toService).then(res=>{
this.toastText='';
this.toastShow=true;
// history.go(-1)
},res=>{

this.toastText='';
this.toastShow=true;
console.log(res)
})


}
}
}
}



